    align-items: center;
}

.menu-buttons {
    justify-content: center;
    gap: 3rem;
    width: 80%;
    height: 60vh;
}

.menu-buttons .btn {
    width: 80%;
    padding: 1rem;
    font-size: 1.5rem;
}